import {
    ADD_SLIDE,
    DELETE_SLIDE,
    INSERT_SLIDE,
    LOWER_SLIDE,
    SET_ACTIVE_SLIDE, SET_SLIDE_PRICE, SET_SLIDE_TIME, SET_SLIDE_TYPE,
    UPPER_SLIDE
} from "@/store/mutations/mutation-types";

export default {
    [ADD_SLIDE](state, newSlide) {
        state.slides.push(newSlide)
    },
    [SET_ACTIVE_SLIDE](state, slideId) {
        state.activeSlideId = slideId
    },
    [INSERT_SLIDE](state, {newSlide, afterSlideId}) {
        state.slides.splice(state.slides.findIndex(slide => slide.id === afterSlideId) + 1, 0, newSlide)
    },
    [UPPER_SLIDE](state, slideId) {
        const slide = state.slides.find(slide => slide.id === slideId)
        const oldIndex = state.slides.indexOf(slide)
        state.slides.splice(oldIndex, 1)
        state.slides.splice(oldIndex - 1, 0, slide)
    },
    [LOWER_SLIDE](state, slideId) {
        const slide = state.slides.find(slide => slide.id === slideId)
        const oldIndex = state.slides.indexOf(slide)
        state.slides.splice(oldIndex, 1)
        state.slides.splice(oldIndex + 1, 0, slide)
    },
    [DELETE_SLIDE](state, slideId) {
        state.slides.splice(state.slides.findIndex(slide => slide.id === slideId), 1)
    },
    [SET_SLIDE_TYPE](state, type) {
        let activeSlide = state.slides.find(slide => slide.id === state.activeSlideId)
        activeSlide.type = parseInt(type)
    },
    [SET_SLIDE_TIME](state, time) {
        let activeSlide = state.slides.find(slide => slide.id === state.activeSlideId)
        activeSlide.time = time
    },
    [SET_SLIDE_PRICE](state, price) {
        let activeSlide = state.slides.find(slide => slide.id === state.activeSlideId)
        activeSlide.price = price
    },
}