import Vue from 'vue'
import Vuex from 'vuex'
import sidebarMutations from './mutations/slide-mutations'
import editorMutations from './mutations/editor-mutations'
import slideMutations from './mutations/elements-mutations'
import sidebarActions from './actions/sidebar-actions'
import slideActions from './actions/slide-actions'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        slideParams: {
            height: 0,
            widthCells: 16 * 4,
            heightCells: 9 * 4,
            cellSize: 0,
            showGrid: true,
            isEntirelyVisible: false,
        },

        activeSlideId: null,
        slides: [],

        slideTypes: [
            {
                id: 1,
                name: 'ТЕСТ: Да/Нет',
                component: 'YesNo',
            },
            {
                id: 2,
                name: 'ТЕСТ: Единственный выбор',
                component: 'SingleChoice',
            },
            {
                id: 3,
                name: 'ТЕСТ: Множественный выбор',
                component: 'MultipleChoice',
            },
            // {
            //     id: 4,
            //     name: 'ТЕСТ: Текстовый ответ',
            //     component: 'TextAnswer',
            // },
            // {
            //     id: 5,
            //     name: 'ОПРОС: Анкетирование',
            //     component: 'Questioning',
            // },
            {
                id: 6,
                name: 'ОПРОС: Голосование',
                component: 'Voting',
            },
            // {
            //     id: 7,
            //     name: 'Задача',
            //     component: 'Task',
            // },
            {
                id: 8,
                name: 'Информация',
                component: 'Info',
            },
        ],
    },
    getters: {
        defaultWysiwygMathConfiguration: () => {
            return ['textStyle', 'fontSize', 'textAlign', 'lists', 'linkInsert', 'codeInsert', 'formulaInsert', 'redo']
        },
        slideIds: (state) => {
            return state.slides.map(slide => slide.id)
        },
        activeSlide: (state) => {
            return state.slides.find(slide => slide.id === state.activeSlideId)
        },
        activeSlideElements: (state, getters) => {
            return getters.activeSlide.slideElements
        },
        activeSlideComponent: (state, getters) => {
            if (getters.activeSlide.type === -1) return null
            return state.slideTypes.find(type => type.id === getters.activeSlide.type).component
        },
    },
    mutations: {
        // increaseNewSlideId(state) {
        //     state.newSlideId++
        // },
        // increaseNewSlideElementId(state) {
        //     state.newSlideElementId++
        // },
        ...sidebarMutations,
        ...editorMutations,
        ...slideMutations,
    },
    actions: {
        ...sidebarActions,
        ...slideActions,
    },
    modules: {},
    strict: process.env.NODE_ENV !== 'production',
})
