<template>
  <div
      class="preview"
      ref="preview"
      :style="previewStyles"
  >
    <RatioKeeper
        v-if="ready && slideTypeSet"
        :ratio="ratio"
        :min-height="height"
        :fit-content="true"
    >
      <div
          class="slide"
          ref="slide"
          :key="slideState"
      >
        <SlideElement
            v-for="slideElement in slide.slideElements"
            :key="slideElement.id"
            v-bind="{element: slideElement, editMode, slideWidth, slideHeight, cellSize}"
        />
      </div>
    </RatioKeeper>

    <div v-else>{{ slideId }}</div>
  </div>
</template>

<script>
import RatioKeeper from "@/components/utils/RatioKeeper";
import SlideElement from "@/components/SlideElement";
import {mapState} from "vuex";

export default {
  name: 'SlidePreview',
  props: ['slideId'],
  // eslint-disable-next-line vue/no-unused-components
  components: {RatioKeeper, SlideElement},
  data() {
    return {
      editMode: false,
      slideState: 1,
      updateTimeout: null,

      ready: false,
      ratio: 16 / 9,
      previewWidth: '100%',
      previewHeight: 0,
    }
  },
  computed: {
    ...mapState({
      height: state => state.slideParams.height,
      slideWidth: state => state.slideParams.widthCells,
      slideHeight: state => state.slideParams.heightCells,
      cellSize: state => state.slideParams.cellSize,
      showGrid: state => state.slideParams.showGrid,
    }),
    previewStyles() {
      return {
        width: this.previewWidth,
        height: this.previewHeight,
      }
    },
    isShowingGrid() {
      return this.editMode && this.showGrid
    },
    slide() {
      return this.$store.state.slides.find(slide => slide.id === this.slideId)
    },
    slideTypeSet() {
      return this.slide.type !== -1
    },
  },
  watch: {
    slide: {
      deep: true,
      handler: function () {
        clearTimeout(this.updateTimeout)
        this.updateTimeout = setTimeout(() => this.slideState++, 1000)
      },
    },
  },
  methods: {},
  mounted() {
    this.previewWidth = this.$refs.preview.clientWidth + 'px'
    this.previewHeight = this.$refs.preview.clientWidth / (this.ratio) + 'px'
    this.ready = true
  }
}
</script>

<style scoped lang="scss">
.preview {
  align-self: flex-end;

  width: 100%;

  border: solid #a0a0a0 1px;
  border-radius: 4px;
  background-color: white;
  overflow: hidden;

  display: flex;
  justify-content: center;
  align-items: center;

  * {
    pointer-events: none;
  }

  ::v-deep.content {
    padding: 0 !important;
  }

  .slide {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: white;
  }
}
</style>
