import {
    ADD_SLIDE_ACTION,
    DUPLICATE_SLIDE_ACTION,
    DELETE_SLIDE_ACTION,
    UPPER_SLIDE_ACTION, LOWER_SLIDE_ACTION, SET_SLIDE_TIME_ACTION
} from "@/store/actions/action-types";
import {
    ADD_SLIDE,
    DELETE_SLIDE,
    INSERT_SLIDE,
    LOWER_SLIDE,
    SET_ACTIVE_SLIDE,
    UPPER_SLIDE
} from "@/store/mutations/mutation-types";

let slideIdCounter = 1
const defaultSlideTime = 60

export default {
    [ADD_SLIDE_ACTION]({commit, dispatch}) {
        new Promise((resolve) => {
            // resolve({id: slideIdCounter++, type: 1, slideElements: []})
            resolve({id: slideIdCounter++, type: -1, time: 0, price: 0, slideElements: []})
        }).then(newSlide => {
            commit(ADD_SLIDE, newSlide)
            return newSlide
        }).then(newSlide => commit(SET_ACTIVE_SLIDE, newSlide.id))
            .then(() => dispatch(SET_SLIDE_TIME_ACTION, defaultSlideTime))
    },
    [UPPER_SLIDE_ACTION]({commit}, slideId) {
        new Promise((resolve) => {
            resolve(slideId)
        }).then(slideId => commit(UPPER_SLIDE, slideId))
    },
    [LOWER_SLIDE_ACTION]({commit}, slideId) {
        new Promise((resolve) => {
            resolve(slideId)
        }).then(slideId => commit(LOWER_SLIDE, slideId))
    },
    [DUPLICATE_SLIDE_ACTION]({commit, state}, slideId) {
        let slide = state.slides.find(slide => slide.id === slideId)
        let newSlide = JSON.parse(JSON.stringify(slide))
        newSlide.id += 100
        newSlide.slideElements.forEach(el => el.id += 1000)

        new Promise((resolve) => {
            resolve(newSlide)
        }).then(newSlide => commit(INSERT_SLIDE, {newSlide, afterSlideId: slideId}))
    },
    [DELETE_SLIDE_ACTION]({commit}, slideId) {
        new Promise((resolve) => {
            resolve()
        }).then(() => commit(DELETE_SLIDE, slideId))
    },
}