<template>
  <div
      class="answers_object"
      :tabindex="0"
      @focusin="focus(false)"
      @focusout="onFocusOut"
  >
    <div
        v-for="(column, index) in columns"
        :key="index"
        class="column"
    >
      <div
          v-for="answer in column"
          :key="answer.id"
          class="answer"
          :style="answerStyles"
      >
        <div
            v-if="isValuable"
            class="checkbox"
            :class="{visible: answer.is_correct}"
            @click="setCorrect(answer); updateValue()"
        >
          <i class="fas fa-check"></i>
        </div>

        <wysiwyg-math
            ref="wysimaths"
            :functions="$store.getters.defaultWysiwygMathConfiguration"
            :html="answer.text"
            :edit-mode="editMode"
            @input="answer.text = $event; updateValue()"
        ></wysiwyg-math>
      </div>
    </div>
  </div>
</template>

<script>
import {ADD_ANSWER_ACTION, REMOVE_ANSWER_ACTION} from "@/store/actions/action-types";
import WysiwygMath from "@/components/wysimath/wysiMath";

export default {
  name: 'AnswersObject',
  components: {WysiwygMath},
  props: ['value', 'editMode'],
  data() {
    return {
      isActive: false,

      answerMargin: 4,
      answers: [],
      columnsCount: 1,
    }
  },
  computed: {
    isFixedAnswersCount() {
      return !!this.value.fixedAnswersCount
    },
    isValuable() {
      return !!this.value.isValuable
    },
    isSingleChoice() {
      return !!this.value.isSingleChoice
    },

    columns() {
      let result = new Array(this.columnsCount).fill(0).map(() => [])

      const minAnswersPerColumn = Math.floor(this.answers.length / this.columnsCount)
      let extraAnswers = this.answers.length % this.columnsCount
      let startIndex = 0, endIndex = 0, indexOffset = 0

      for (let columnIndex = 0; columnIndex < this.columnsCount; columnIndex++) {
        startIndex = columnIndex * minAnswersPerColumn + indexOffset
        endIndex = (columnIndex + 1) * minAnswersPerColumn + indexOffset
        if (extraAnswers > 0) {
          endIndex++
          extraAnswers--
          indexOffset++
        }
        result[columnIndex] = this.answers.slice(startIndex, endIndex)
      }

      return result
    },
    answerStyles() {
      return {
        'max-height': `calc(${Math.floor(100 / (Math.ceil(this.answers.length / this.columnsCount)))}% - ${this.answerMargin * 2}px)`,
        'margin': this.answerMargin + 'px',
      }
    },
  },
  watch: {
    value: {
      handler: function () {
        this.parseValue()
      },
      deep: true,
    },
    isActive(newVal) {
      if (newVal && !this.mathWasRendered) {
        this.mathWasRendered = true
        window.renderMathInElement(document.body)
      }
    },
  },
  methods: {
    parseValue() {
      this.answers = JSON.parse(JSON.stringify(this.value.answers))
      this.columnsCount = this.value.columnsCount
    },

    updateActions() {
      let actions = []
      if (this.isFixedAnswersCount) {
        actions = [
          {html: '<i class="far fa-edit"></i>', func: this.focus},
          {html: '<b>l</b>', func: () => this.columnsCount = 1},
          {html: '<b>ll</b>', func: () => this.columnsCount = 2},
          {html: '<b>lll</b>', func: () => this.columnsCount = 3},
        ]
      } else {
        actions = [
          {html: '<i class="far fa-edit"></i>', func: this.focus},
          {html: '<i class="fas fa-plus"></i>', func: this.addAnswer},
          {html: '<i class="fas fa-minus"></i>', func: this.removeLastAnswer},
          {html: '<b>l</b>', func: () => this.columnsCount = 1},
          {html: '<b>ll</b>', func: () => this.columnsCount = 2},
          {html: '<b>lll</b>', func: () => this.columnsCount = 3},
        ]
      }

      this.$emit('initialized', {
        actions: actions,
        onDblClick: this.focus,
      })
    },
    updateValue() {
      this.$emit('input', {answers: this.answers, columnsCount: this.columnsCount})
    },

    focus(shouldFocusAnswer = true) {
      this.isActive = true
      this.$emit('focusin')
      if (shouldFocusAnswer)
        this.$refs.wysimaths[0].focus()
    },

    onFocusOut() {
      this.isActive = false
      this.$emit('focusout')
    },

    addAnswer() {
      this.$store.dispatch(ADD_ANSWER_ACTION).then(newAnswer => {
        this.answers.push(newAnswer)
        this.updateValue()
      })
    },
    removeLastAnswer() {
      if (this.answers.length > 1) {
        this.$store.dispatch(REMOVE_ANSWER_ACTION, this.answers[this.answers.length - 1].id)
            .then(() => {
              this.answers.pop()
              this.updateValue()
            })
      } else
        alert('Нельзя удалить последний ответ')
    },
    setCorrect(answer) {
      if (answer.is_correct) {
        const isOnlyOneCorrect = this.answers.filter(answer => answer.is_correct).length === 1
        if (isOnlyOneCorrect) {
          alert('Хотя бы один ответ должен быть правильным')
        } else {
          answer.is_correct = false
        }
      } else {
        if (this.isSingleChoice) {
          this.answers.forEach(answer => answer.is_correct = false)
        }
        answer.is_correct = true
      }
    },
  },
  created() {
    this.parseValue()
  },
  mounted() {
    this.updateActions()
  },
}
</script>

<style scoped lang="scss">
.answers_object {
  display: flex;

  .column {
    flex: 1 0 0;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    $border: 2px solid #6f6f6f;
    $answerMargin: 4px;

    .answer {
      flex: 1 0 0;
      display: flex;

      border: $border;
      overflow: hidden;

      .checkbox {
        display: flex;
        justify-content: center;
        align-items: center;

        border-right: $border;
        padding: .2em .4em;

        i {
          opacity: 0;
        }

        &.visible {
          background-color: lightgreen;

          i {
            opacity: 1;
          }
        }
      }

      ::v-deep .editor {
        border: 1px solid transparent;

        &:focus {
          box-shadow: none !important;
        }
      }
    }
  }
}
</style>
