<template>
  <div class="text_object">
    <wysiwyg-math
        ref="wysimath"
        :functions="$store.getters.defaultWysiwygMathConfiguration"
        :html="value.html"
        :edit-mode="editMode"
        @input="$emit('input', {html: $event})"
        @focusin="$emit('focusin')"
        @focusout="onFocusOut"
    ></wysiwyg-math>
  </div>
</template>

<script>
import WysiwygMath from "@/components/wysimath/wysiMath";

export default {
  name: 'TextObject',
  components: {WysiwygMath},
  props: ['value', 'editMode'],
  data() {
    return {
      isActive: false,
      mathWasRendered: false,
    }
  },
  watch: {
    isActive(newVal) {
      if (newVal && !this.mathWasRendered) {
        this.mathWasRendered = true
        window.renderMathInElement(document.body)
      }
    },
  },
  methods: {
    focus() {
      this.isActive = true
      this.$refs.wysimath.focus()
      this.$emit('focusin')
    },

    onFocusOut() {
      this.isActive = false
      this.$emit('focusout')
    },
  },
  mounted() {
    this.$emit('initialized', {
      actions: [
        {html: '<i class="far fa-edit"></i>', func: this.focus},
      ],
      onDblClick: this.focus,
    })
  },
}
</script>

<style scoped lang="scss">
.text_object {
  ::v-deep .editor {
    border: 1px solid transparent;

    &:focus {
      box-shadow: none !important;
    }
  }
}
</style>
