<template>
  <div
      ref="movable"
      class="movable"
      :class="movableClasses"
      :tabindex="element.id"
      @dblclick="onDblClick"
      @focusin="isElementFocused = true"
      @focusout="isElementFocused = false"
  >
    <span class="popup"
          :class="{'hidden': noActions || !isOnlyElementFocused}"
          @dblclick.stop
    >
      <span class="d-flex">
        <button
            v-for="(action, index) in actions"
            :key="index"
            class="popup-button"
            :class="{pressed: action.isPressed}"
            :disabled="action.disabled"
            @click="action.func"
            v-html="action.html"
        >
        </button>

        <button
            v-if="!noDelete"
            class="popup-button"
            @click="deleteSelf"
        >
          <i class="far fa-trash-alt"></i>
        </button>
      </span>
    </span>

    <template v-if="isOnlyElementFocused && !noResize">
      <div class="size_change top" ref="top"></div>
      <div class="size_change right" ref="right"></div>
      <div class="size_change bottom" ref="bottom"></div>
      <div class="size_change left" ref="left"></div>
    </template>

    <!--    <div>-->
    <!--      {{ position }}-->
    <!--      <br>{{ size }}-->

    <!--      &lt;!&ndash;      <b>{{ element.id }}</b>&ndash;&gt;-->
    <!--      &lt;!&ndash;      <br>{{ position }}&ndash;&gt;-->
    <!--      &lt;!&ndash;      <br>{{ size }}&ndash;&gt;-->
    <!--      &lt;!&ndash;      <br>isElementFocused: {{ isElementFocused }}&ndash;&gt;-->
    <!--      &lt;!&ndash;      <br>isInnerFocused: {{ isInnerFocused }}&ndash;&gt;-->
    <!--      &lt;!&ndash;      <br>isMoving: {{ isMoving }}&ndash;&gt;-->
    <!--      &lt;!&ndash;      <br>isOutside: {{ isOutside }}&ndash;&gt;-->
    <!--    </div>-->

    <!--    <input-->
    <!--        ref="content"-->
    <!--        @focusin="isContentFocused = true"-->
    <!--        @focusout="isContentFocused = false"-->
    <!--    >-->

    <component
        :is="element.content.component"
        :value="element.content.value"
        :edit-mode="editMode"
        @input="setValue({id: element.id, value: $event})"
        class="inner_component"
        ref="inner_component"
        @focusin="isInnerFocused = true"
        @focusout="isInnerFocused = false"
        @initialized="onInnerInitialized"
    />
  </div>
</template>

<script>
import TextObject from "@/components/slide-elements/TextObject";
import ImageObject from "@/components/slide-elements/image-object/ImageObject";
import YouTubeObject from "@/components/slide-elements/youtube-object/YouTubeObject";
import {
  DELETE_SLIDE_ELEMENT,
  MOVE_SLIDE_ELEMENT,
  RESIZE_SLIDE_ELEMENT,
  SET_SLIDE_ELEMENT_VALUE
} from "@/store/mutations/mutation-types";
import {mapMutations} from "vuex";
import AnswersObject from "@/components/slide-elements/AnswersObject";
import TimerObject from "@/components/slide-elements/TimerObject";

export default {
  name: 'SlideElement',
  components: {
    TimerObject,
    TextObject,
    ImageObject,
    YouTubeObject,
    AnswersObject,
  },
  props: ['element', 'editMode', 'slideWidth', 'slideHeight', 'cellSize'],
  inject: {
    canDeleteElement: {
      default: () => {
      }
    }
  },
  data() {
    return {
      // isFocused: false,
      // isSelected: false,
      isElementFocused: false,
      isInnerFocused: false,
      isMoving: false,

      defaultZ: '20',
      activeZ: '21',

      onDblClick: () => {
      },
      actions: [],

      x: 0,
      y: 0,
      width: 0,
      height: 0,
      noActions: false,
      noResize: false,
      noDelete: false,
    }
  },
  computed: {
    position() {
      return [this.x, this.y]
    },
    size() {
      return [this.width, this.height]
    },
    isOutside() {
      return this.x < 0
          || this.y < 0
          || this.x + this.width > this.slideWidth
          || this.y + this.height > this.slideHeight
    },
    isOnlyElementFocused() {
      return this.isElementFocused && !this.isInnerFocused
    },
    movableClasses() {
      return {
        focused: this.isElementFocused,
        selected: this.isInnerFocused,
        outside: this.isOutside,
        inactive: !this.isElementFocused && !this.isInnerFocused,
        bordered: this.editMode && this.$store.state.slideParams.showGrid
      }
    },
  },
  watch: {
    position() {
      if (this.editMode)
        this.moveSlideElement({
          id: this.element.id,
          x: this.x,
          y: this.y,
        })

      this.$refs.movable.style.left = this.x * this.cellSize + 'px'
      this.$refs.movable.style.top = this.y * this.cellSize + 'px'
    },
    size() {
      if (this.editMode)
        this.resizeSlideElement({
          id: this.element.id,
          width: this.width,
          height: this.height,
        })

      this.$refs.movable.style.width = this.width * this.cellSize + 'px'
      this.$refs.movable.style.height = this.height * this.cellSize + 'px'
    },
    isElementFocused(newVal) {
      if (newVal) {
        this.$nextTick().then(() => {
          this.initializeSizeChanger(this.$refs.top, false, true)
          this.initializeSizeChanger(this.$refs.right, true, false)
          this.initializeSizeChanger(this.$refs.bottom, false, false)
          this.initializeSizeChanger(this.$refs.left, true, true)
        })
      }
    }
  },
  methods: {
    ...mapMutations({
      moveSlideElement: MOVE_SLIDE_ELEMENT,
      resizeSlideElement: RESIZE_SLIDE_ELEMENT,
      setElementValue: SET_SLIDE_ELEMENT_VALUE,
    }),

    getPosition() {
      return {x: this.x, y: this.y}
    },
    getSize() {
      return {width: this.width, height: this.height}
    },
    setValue(payload) {
      if (this.editMode)
        this.setElementValue(payload)
    },

    moveSelfInside() {
      if (this.isOutside) {
        if (this.x < 0) this.x = 0
        if (this.y < 0) this.y = 0
        if (this.x + this.width > this.slideWidth) this.x = this.slideWidth - this.width
        if (this.y + this.height > this.slideHeight) this.y = this.slideHeight - this.height
      }

      // still outside
      if (this.isOutside) {
        if (this.width > this.slideWidth) this.width = this.slideWidth
        if (this.height > this.slideHeight) this.height = this.slideHeight
        this.moveSelfInside()
      }
    },

    initializeMovable() {
      let el = this.$refs.movable
      el.ondragstart = function () {
        return false;
      };

      el.onmousedown = (event) => {
        if (this.isInnerFocused) return

        this.isMoving = true
        el.style.zIndex = this.activeZ
        let initialX = event.clientX, initialY = event.clientY

        let posBeforeMove = this.getPosition()

        let onMouseMove = (event) => {
          let movedCellsX = Math.round((event.clientX - initialX) / this.cellSize)
          let movedCellsY = Math.round((event.clientY - initialY) / this.cellSize)

          this.x = posBeforeMove.x + movedCellsX
          this.y = posBeforeMove.y + movedCellsY
        }

        document.addEventListener('mousemove', onMouseMove);

        document.onmouseup = () => {
          this.moveSelfInside()

          this.isMoving = false
          el.style.zIndex = this.defaultZ
          document.removeEventListener('mousemove', onMouseMove);
          document.onmouseup = null;
        }
      };
    },

    initializeSizeChanger(el, isHorizontal, shouldChangePos) {
      if (el === undefined) return

      el.ondragstart = function () {
        return false;
      };

      el.onmousedown = (event) => {
        event.stopPropagation()

        this.isMoving = true
        el.style.zIndex = this.activeZ
        let initialX = event.clientX, initialY = event.clientY

        let sizeBeforeMove = this.getSize()
        let posBeforeMove = this.getPosition()

        let onMouseMove = (event) => {
          let movedCellsX = Math.round((event.clientX - initialX) / this.cellSize)
          let movedCellsY = Math.round((event.clientY - initialY) / this.cellSize)

          if (isHorizontal) {
            if (shouldChangePos) {
              this.x = posBeforeMove.x + movedCellsX
              this.width = sizeBeforeMove.width - movedCellsX
            } else {
              this.width = sizeBeforeMove.width + movedCellsX
            }
          } else {
            if (shouldChangePos) {
              this.y = posBeforeMove.y + movedCellsY
              this.height = sizeBeforeMove.height - movedCellsY
            } else {
              this.height = sizeBeforeMove.height + movedCellsY
            }
          }
        }

        document.addEventListener('mousemove', onMouseMove);

        document.onmouseup = () => {
          this.moveSelfInside()

          this.isMoving = false
          el.style.zIndex = this.defaultZ
          document.removeEventListener('mousemove', onMouseMove);
          document.onmouseup = null;
        }
      };
    },

    deleteSelf() {
      let trueOrMessage = this.canDeleteElement(this.element.content.component)
      if (trueOrMessage === true)
        this.$store.commit(DELETE_SLIDE_ELEMENT, this.element.id)
      else alert(trueOrMessage)
    },
    onKeyUp(event) {
      if (this.isOnlyElementFocused) {
        if (event.key === 'Delete')
          this.deleteSelf()
      }
    },

    onInnerInitialized(data) {
      this.actions = data.actions
      if (data.onDblClick)
        this.onDblClick = data.onDblClick
    },

    test() {
      console.log('test')
    }
  },
  created() {
    // console.log(JSON.parse(JSON.stringify(this.element)))
    Object.entries(this.element).forEach(([k, v]) => this[k] = v)
  },
  mounted() {
    if (this.editMode)
      this.initializeMovable()
  },
}
</script>

<style scoped lang="scss">
@import "src/assets/styles/colors";

$defaultZ: 20;

@mixin visible_border() {
  border: 2px rgba(217, 217, 217, 1) dashed;
}

@mixin invisible_border() {
  border: 2px transparent dashed;
}

.movable {
  position: absolute;
  z-index: $defaultZ;

  @include invisible_border;
  background-color: white;
  cursor: move;
  user-select: none;
  outline: none;

  //padding: 4px;
  //padding: 2px;

  //display: flex;
  //justify-content: center;
  //align-items: center;

  &.bordered {
    @include visible_border;
  }

  &:hover {
    @include invisible_border;
    outline: 2px $primary solid;
    border-radius: 2px;
  }

  .inner_component {
    pointer-events: none;
  }

  &.outside {
    opacity: .3;

    .popup {
      display: none;
    }
  }

  &.focused {
    @include invisible_border;
    outline: 3px $primary solid;
    border-radius: 2px;
    z-index: $defaultZ + 1 !important;
  }

  &.selected {
    cursor: default;

    .inner_component {
      pointer-events: auto;
    }
  }

  .size_change {
    $squareSize: 10px;
    width: $squareSize;
    height: $squareSize;
    background-color: $primary;
    position: absolute;
    z-index: $defaultZ + 2 !important;

    $moverOffset: 4px;

    &.top {
      top: 0;
      left: 50%;
      transform: translateY(calc(-50% - #{$moverOffset})) translateX(-50%);
      cursor: ns-resize;
    }

    &.right {
      right: 0;
      top: 50%;
      transform: translateX(calc(50% + #{$moverOffset})) translateY(-50%);
      cursor: ew-resize;
    }

    &.bottom {
      bottom: 0;
      left: 50%;
      transform: translateY(calc(50% + #{$moverOffset})) translateX(-50%);
      cursor: ns-resize;
    }

    &.left {
      left: 0;
      top: 50%;
      transform: translateX(calc(-50% - #{$moverOffset})) translateY(-50%);
      cursor: ew-resize;
    }
  }

  .popup {
    background-color: #f3f3f3;
    color: #000000;
    text-align: center;
    border-radius: 6px;
    padding: 3px 0;
    position: fixed;
    margin-left: calc(-1 * var(--scrollLeft));
    margin-top: calc(-28px - 1rem - var(--scrollTop));
    z-index: 1000;
    border: 1px solid #c4c4c4;
    display: flex;
    align-items: center;

    font-size: 1rem;

    &.hidden {
      position: fixed !important;
      top: 0;
      opacity: 0;
    }

    &::before, &::after {
      content: "";
      position: absolute;
      top: 96%;
      left: 12px;
      margin-left: -5px;
      border-width: 7px;
      border-style: solid;
      border-color: #f3f3f3 transparent transparent transparent;
    }

    &::before {
      top: 100%;
      border-color: #c4c4c4 transparent transparent transparent;
    }

    & > span {
      padding: 0 4px;
    }

    .popup-button {
      border: none;
      line-height: 1;
      background-color: inherit;
      padding: 4px;
      margin: 0 1px;
      width: 27px;
      border-radius: 3px !important;

      &.pressed {
        background-color: #bfbfbf !important;
      }
    }

    .popup-button:hover {
      background-color: #dedede;
    }

    .selected {
      background-color: #bfbfbf !important;
    }
  }

  .inner_component {
    //flex: 1 1 auto;

    width: 100%;
    height: 100%;
    overflow: hidden;
  }
}
</style>
