<template>
  <div class="slide_editor">
    <div class="params_area border-bottom">
      <div class="row px-3">
        <div class="col-5 col-md-4 col-lg-3">
          <label
              for="slideTypeSelect"
              class="form-label"
          >
            Тип слайда
          </label>
          <select class="form-select"
                  id="slideTypeSelect"
                  :value="activeSlide.type"
                  @change="setSlideTypeAction($event.target.value)"
          >
            <option :value="-1" disabled>Выберите тип слайда</option>
            <option v-for="type in $store.state.slideTypes"
                    :key="type.id"
                    :value="type.id"
            >{{ type.name }}
            </option>
          </select>
        </div>

        <template
            v-if="$store.getters.activeSlideComponent !== null"
        >
          <div class="col-4 col-md-3 col-lg-2">
            <label
                for="slideTimeInput"
                class="form-label"
            >
              Время показа
            </label>
            <input
                type="number"
                class="form-control"
                id="slideTimeInput"
                placeholder="Введите число"
                :value="activeSlide.time"
                @input="setSlideTimeAction($event.target.value)"
                min="0"
            >
          </div>
        </template>

        <slot name="params"></slot>
      </div>
    </div>

    <div
        v-if="noControls"
        class="help_text"
    >Выберите нужный тип слайда
    </div>

    <div
        v-else
        class="slide_controls_area"
    >
      <div
          class="slide_area"
          ref="slide_area"
          :style="slideAreaStyles"
      >
        <RatioKeeper
            :ratio="16 / 9"
            :min-height="500"
            :fit-content="keeperFitContent"
            :class="{zoom_cursor: keeperFitContent}"
            @mousedown.native="onKeeperClick"
        >
          <Slide
              :slide-id="activeSlide.id"
              :edit-mode="isInEditMode"
          />
        </RatioKeeper>
      </div>

      <div class="controls_area border-start">
        <button
            class="controls_area__button"
            @click="addText"
        >
          <i class="fas fa-align-justify"></i>
          Текст
        </button>

        <button
            class="controls_area__button"
            @click="addImage"
        >
          <i class="far fa-image"></i>
          Картинка
        </button>

        <button
            class="controls_area__button"
            @click="addYouTube"
        >
          <i class="fab fa-youtube"></i>
          YouTube
        </button>

        <slot name="controls"></slot>

        <div style="flex: 1 1 auto"></div>

        <!--        <button-->
        <!--            class="controls_area__temp_button"-->
        <!--            @click="isInEditMode = !isInEditMode"-->
        <!--        >{{ isInEditMode ? 'Посмотреть' : 'Скрыть' }} результат-->
        <!--        </button>-->

        <button
            class="controls_area__temp_button"
            @click="toggleScrollbars"
        >{{ $store.state.slideParams.isEntirelyVisible ? 'Показать' : 'Скрыть' }} скролл
        </button>

        <button
            class="controls_area__temp_button"
            @click="toggleGrid"
        >{{ $store.state.slideParams.showGrid ? 'Скрыть' : 'Показать' }} сетку
        </button>

        <button
            class="controls_area__temp_button"
            @click="logState"
        >Log vuex state
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import {mapActions, mapGetters, mapMutations, mapState} from "vuex";
import {
  ADD_TEXT_ELEMENT,
  ADD_IMAGE_ELEMENT,
  ADD_YOUTUBE_ELEMENT,
  SET_SLIDE_TYPE_ACTION, SET_SLIDE_TIME_ACTION
} from "@/store/actions/action-types";
import RatioKeeper from "@/components/utils/RatioKeeper";
import Slide from "@/components/Slide";
import {TOGGLE_GRID, TOGGLE_SCROLLBARS} from "@/store/mutations/mutation-types";

export default {
  name: 'SlideEditor',
  components: {Slide, RatioKeeper},
  props: {
    noControls: Boolean,
  },
  data() {
    return {
      isInEditMode: true,

      slideAreaStyles: {
        '--scrollLeft': '0px',
        '--scrollTop': '0px',
      },
    }
  },
  computed: {
    ...mapState({
      keeperFitContent: state => state.slideParams.isEntirelyVisible,
    }),
    ...mapGetters(['activeSlide']),
  },
  methods: {
    ...mapMutations({
      toggleGrid: TOGGLE_GRID,
      toggleScrollbars: TOGGLE_SCROLLBARS,
    }),
    ...mapActions({
      setSlideTypeAction: SET_SLIDE_TYPE_ACTION,
      setSlideTimeAction: SET_SLIDE_TIME_ACTION,
    }),
    onKeeperClick(event) {
      if (this.keeperFitContent) {
        event.preventDefault()
        this.toggleScrollbars()
      }
    },
    addText() {
      this.$store.dispatch(ADD_TEXT_ELEMENT, {x: 3, y: 1, width: 38, height: 23})
    },
    addImage() {
      this.$store.dispatch(ADD_IMAGE_ELEMENT, {x: 43, y: 1, width: 18, height: 11})
    },
    addYouTube() {
      this.$store.dispatch(ADD_YOUTUBE_ELEMENT, {x: 43, y: 13, width: 18, height: 11})
    },
    logState() {
      console.log(this.$store.state)
    },
    updateScrollValues() {
      this.slideAreaStyles['--scrollLeft'] = this.$refs.slide_area.scrollLeft + 'px'
      this.slideAreaStyles['--scrollTop'] = this.$refs.slide_area.scrollTop + 'px'
    },
  },
  mounted() {
    if (!this.noControls) {
      this.$refs.slide_area.addEventListener('scroll', this.updateScrollValues);
    }
  }
}
</script>

<style lang="scss">
@import "src/assets/styles/colors";

.slide_editor {
  display: flex;
  flex-direction: column;
  overflow: hidden;

  .params_area {
    height: 80px;

    .form-label {
      margin-top: .2rem;
      margin-bottom: .2rem;
    }
  }

  .help_text {
    flex: 1 1 auto;
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 0 5% 5%;

    font-size: 2rem;
    color: #8c8c8c;
  }

  .slide_controls_area {
    flex: 1 1 auto;
    overflow: hidden;
    display: flex;

    .slide_area {
      flex: 1 1 auto;
      overflow: auto;
      background-color: #f1f2f5;

      & > * > div.sidebar_item {
        box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.2);
      }
    }

    .controls_area {
      min-width: 110px;
      width: 110px;
      background-color: #f8f9fb;

      display: flex;
      flex-direction: column;

      .controls_area__temp_button {
        margin: 0 8px 8px;
        font-size: 0.8rem;
        height: 50px;
      }

      .controls_area__button {
        margin: 8px 8px 0;

        &:first-child {
          margin-top: 12px;
        }

        border: none;
        background-color: transparent;
        border-radius: 4px;

        padding: 4px 8px;
        color: $primary;

        display: flex;
        flex-direction: column;
        align-items: center;

        i {
          font-size: 1.5rem;
          padding: 4px;
        }

        &:hover:enabled {
          background-color: rgba(0, 0, 0, 0.05);
        }

        &:active:enabled {
          background-color: rgba(0, 0, 0, 0.1);
        }

        &:disabled {
          opacity: .3;
        }
      }
    }
  }
}

.zoom_cursor {
  cursor: zoom-in;

  .slide {
    pointer-events: none;
  }
}
</style>
