<template>
  <SlideEditor id="slide_editor">
    <template #params>
      <div class="col-4 col-md-3 col-xl-2">
        <label
            for="slidePriceInput"
            class="form-label"
        >
          Кол-во баллов
        </label>
        <input
            type="number"
            class="form-control"
            id="slidePriceInput"
            placeholder="Введите число"
            :value="activeSlide.price"
            @input="setSlidePrice($event.target.value)"
            min="0"
        >
      </div>
    </template>

    <template #controls>
      <button
          class="controls_area__button"
          @click="addAnswer"
      >
        <i class="fas fa-plus"></i>
        Ответ
      </button>
    </template>
  </SlideEditor>
</template>

<script>
import SlideEditor from "@/components/SlideEditor";
import Restricted from "@/components/mixins/Restricted";
import {mapGetters, mapMutations} from "vuex";
import {SET_SLIDE_PRICE} from "@/store/mutations/mutation-types";
import AddAnswer from "@/components/mixins/AddAnswer";

export default {
  name: 'SingleChoice',
  components: {
    SlideEditor,
  },
  mixins: [Restricted, AddAnswer],
  props: {},
  data() {
    return {
      restrictions: {
        // TextObject: {min: 1, max: 2},
      }
    }
  },
  computed: {
    ...mapGetters(['activeSlide'])
  },
  methods: {
    ...mapMutations({
      setSlidePrice: SET_SLIDE_PRICE,
    }),
  },
}
</script>

<style lang="scss" scoped>
</style>
