import Vue from 'vue'
import VueRouter from 'vue-router'
import Editor from '../views/Editor.vue'
import WysimathTest from "@/views/WysimathTest";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        component: Editor
    },
    {
        path: '/wysimath',
        component: WysimathTest
    },
]

const router = new VueRouter({
    base: process.env.BASE_URL,
    routes,
    mode: 'history',
})

export default router
