<template>
  <div
      class="slide"
      ref="slide"
  >
    <div
        v-if="isShowingGrid"
        class="grid"
        :style="{'background-size': backgroundSize}"
    ></div>

    <SlideElement
        v-for="slideElement in slideElements"
        :key="slideElement.id"
        v-bind="{element: slideElement, editMode, slideWidth, slideHeight, cellSize}"
    />
  </div>
</template>

<script>
import SlideElement from "@/components/SlideElement";
import {mapState} from "vuex";
import {SET_SLIDE_HEIGHT} from "@/store/mutations/mutation-types";

export default {
  name: 'Slide',
  components: {SlideElement},
  props: {
    slideId: {
      type: Number,
    },
    editMode: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      backgroundSize: 'calc(100% / 16) calc(100% / 9)',
    }
  },
  computed: {
    ...mapState({
      slideWidth: state => state.slideParams.widthCells,
      slideHeight: state => state.slideParams.heightCells,
      cellSize: state => state.slideParams.cellSize,
      showGrid: state => state.slideParams.showGrid,
    }),
    isShowingGrid() {
      return this.editMode && this.showGrid
    },
    slideElements() {
      return this.$store.state.slides.find(slide => slide.id === this.slideId).slideElements
    },
  },
  methods: {
    setGridSize() {
      this.$store.commit(SET_SLIDE_HEIGHT, this.$refs.slide.clientHeight)
      this.backgroundSize = `${100 / this.slideWidth}% ${100 / this.slideHeight}%`
    },
  },
  mounted() {
    this.setGridSize()
  },
}
</script>

<style scoped lang="scss">
$gridColor: rgb(234, 234, 234);
$gridWidth: 1px;

.slide {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: white;

  .grid {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to right, $gridColor $gridWidth, transparent 1px),
    linear-gradient(to bottom, $gridColor $gridWidth, transparent $gridWidth);
    border-bottom: $gridColor $gridWidth solid;
    border-right: $gridColor $gridWidth solid;
  }
}
</style>
