import {
    ADD_ANSWER_TO_ANSWERS_ELEMENT,
    ADD_SLIDE_ELEMENT, DELETE_SLIDE_ELEMENT,
    MOVE_SLIDE_ELEMENT,
    RESIZE_SLIDE_ELEMENT,
    SET_SLIDE_ELEMENT_VALUE
} from "@/store/mutations/mutation-types";

let slideElIdCounter = 1

export default {
    [ADD_SLIDE_ELEMENT](state, params) {
        let activeSlideElements = state.slides.find(slide => slide.id === state.activeSlideId).slideElements
        activeSlideElements.push({id: slideElIdCounter++, ...params})
    },
    [DELETE_SLIDE_ELEMENT](state, id) {
        let activeSlideElements = state.slides.find(slide => slide.id === state.activeSlideId).slideElements
        let indexOfElement = activeSlideElements.map(el => el.id).indexOf(id)
        activeSlideElements.splice(indexOfElement, 1)
    },
    [MOVE_SLIDE_ELEMENT](state, {id, x, y}) {
        let slideElement = state.slides.find(slide => slide.id === state.activeSlideId).slideElements
            .find(el => el.id === id)
        slideElement.x = x
        slideElement.y = y
    },
    [RESIZE_SLIDE_ELEMENT](state, {id, width, height}) {
        let slideElement = state.slides.find(slide => slide.id === state.activeSlideId).slideElements
            .find(el => el.id === id)
        slideElement.width = width
        slideElement.height = height
    },
    [SET_SLIDE_ELEMENT_VALUE](state, {id, value}) {
        let slideElement = state.slides.find(slide => slide.id === state.activeSlideId).slideElements
            .find(el => el.id === id)
        slideElement.content.value = JSON.parse(JSON.stringify(Object.assign(slideElement.content.value, value)))
    },

    [ADD_ANSWER_TO_ANSWERS_ELEMENT](state, {id, answer}) {
        let slideElement = state.slides.find(slide => slide.id === state.activeSlideId).slideElements
            .find(el => el.id === id)
        slideElement.content.value.answers.push(answer)
    },
}