<template>
  <div v-if="showModal">
    <div
        class="modal d-block" tabindex="-1"
        ref="modal"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Вставить видео YouTube</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"
                    @click="showModal = false"
            ></button>
          </div>

          <div class="modal-body">
            <div class="form-group">
              <label for="urlInput">Ссылка на ролик YouTube:</label>
              <input type="url"
                     class="form-control"
                     id="urlInput"
                     v-model="url"
                     placeholder="https://www.youtube.com/watch?v=???"
              >
            </div>
          </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal"
                    @click="showModal = false"
            >Отмена
            </button>
            <button type="button" class="btn btn-primary"
            @click="addVideo"
            >Добавить</button>
          </div>
        </div>
      </div>
    </div>

    <div class="modal-backdrop fade show"></div>
  </div>
</template>

<script>
export default {
  name: 'linkModal',
  props: {},
  data: function () {
    return {
      showModal: false,
      url: '',
    }
  },
  computed: {},
  watch: {},
  methods: {
    toggle() {
      this.url = ''
      this.showModal = true
    },

    addVideo() {
      let result = this.url.match(/https:\/\/www\.youtube\.com\/watch\?v=(.+)/);
      if (result !== null) {
        this.$emit('set-video-id', result[1])
        this.showModal = false
      } else {
        alert('Неверный формат URL')
        this.url = ''
      }
    },
  },
}
</script>

