<template>
  <transition name="fade">
    <div class="modal-mask" v-show="showModal">
      <div class="modal-wrapper">
        <div class="modal-dialog modal-xl modal-dialog-scrollable" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">Вставить программный код</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close" @click="showModal = false">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="container-fluid">
                <div
                    class="row"
                    style="min-height: 30vh"
                >
                  <div class="col-lg-6 d-flex flex-column">
                    <label
                        for="unformatted_code"
                        class="form-label"
                    >Неформатированный код</label>

                    <textarea
                        id="unformatted_code"
                        class="form-control flex-grow-1"
                        style="width: 100%"
                        v-model="code"
                    ></textarea>
                  </div>

                  <div
                      class="col-lg-6 d-flex flex-column mt-lg-0"
                      style="margin-top: 1rem"
                  >
                    <label
                        class="form-label"
                    >Форматированный код</label>

                    <pre class="flex-grow-1" ref="preformatted"><code ref="formatted_code" :class="languageClass"></code></pre>
                  </div>
                </div>
              </div>
            </div>

            <div class="modal-footer">
              <div>
                <select
                    class="form-select"
                    v-model="language"
                >
                  <option disabled :value="null">Выберите язык</option>
                  <option
                      v-for="lang in languages"
                      :key="lang.value"
                      :value="lang.value"
                  >{{ lang.text }}
                  </option>
                </select>
              </div>

              <div class="flex-grow-1"></div>

              <button type="button" class="btn btn-secondary" @click="showModal = false">Отмена</button>
              <button type="button" class="btn btn-primary"
                      @click="addCode"
              >Добавить
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'codeModal',
  props: {},
  data: function () {
    return {
      showModal: false,

      code: '',
      language: null,
      languages: [
        {text: 'Markup', value: 'markup'},
        {text: 'XML', value: 'xml'},
        {text: 'HTML', value: 'html'},
        {text: 'CSS', value: 'css'},
        {text: 'Си-подобный', value: 'clike'},
        {text: 'JavaScript', value: 'js'},
        {text: 'Python', value: 'python'},
      ],
    }
  },
  computed: {
    languageClass() {
      if (this.language === null)
        return ''
      this.$nextTick(this.formatCode)
      return `language-${this.language}`
    },
  },
  watch: {
    code() {
      this.$refs.formatted_code.textContent = this.code
      this.formatCode()
    },
  },
  methods: {
    toggle() {
      this.showModal = true
      this.code = ''
      this.language = null
    },

    formatCode() {
      window.Prism.highlightElement(this.$refs.formatted_code)
    },

    addCode() {
      this.showModal = false
      this.$emit('insert-code', this.$refs.preformatted)
    },
  },
  mounted() {
    this.formatCode()
  },
}
</script>

<style scoped lang="scss">
@import 'https://stackpath.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css';
@import 'https://use.fontawesome.com/releases/v5.2.0/css/all.css';
@import 'https://fonts.googleapis.com/icon?family=Material+Icons';

#unformatted_code {
  font-family: monospace;
  padding: .8em 1em;
}

pre {
  margin: 0;
  border-radius: 4px;
}

pre, code {
  overflow-wrap: break-word;
  white-space: pre-wrap;
}
</style>
