<template>
  <div class="overlay dark">
    <div
        class="overlay_button hide_button"
        @click="hideSelf"
    >
      <i
          class="fas fa-times"
      ></i>
    </div>

    <div class="content">
      <div
          class="cropper_area"
          ref="cropper_area"
          :style="cropperAreaStyle"
      >
        <img
            :src="imageUrl"
            class="cropping"
            @load="isImageLoaded = true"
        >

        <div
            class="cropper"
            ref="cropper"
            :style="cropperStyle"
        >
          <div class="size_change top" ref="top"></div>
          <div class="size_change right" ref="right"></div>
          <div class="size_change bottom" ref="bottom"></div>
          <div class="size_change left" ref="left"></div>

          <div class="image_wrapper">
            <img
                v-if="isImageLoaded"
                :src="imageUrl"
                :style="croppedImageStyle"
            >
          </div>
        </div>
      </div>
    </div>

    <div
        class="overlay_button ok_button"
        @click="cropImage"
    >Обрезать
    </div>

    <canvas
        ref="canvas"
        :width="width"
        :height="height"
    ></canvas>
  </div>
</template>

<script>
export default {
  name: 'ImageResizeModal',
  components: {},
  props: ['imageUrl'],
  inject: ['createImage'],
  data() {
    return {
      isImageLoaded: false,

      originalWidth: 0,
      originalHeight: 0,

      areaWidth: 0,
      areaHeight: 0,

      cropperAreaStyle: {
        width: '100%',
        height: '100%',
      },

      x: 0,
      y: 0,
      width: 400,
      height: 300,
    }
  },
  computed: {
    cropperStyle() {
      return {
        width: this.width + 'px',
        height: this.height + 'px',
        left: this.x + 'px',
        top: this.y + 'px',
      }
    },
    croppedImageStyle() {
      return {
        width: this.cropperAreaStyle.width,
        height: this.cropperAreaStyle.height,
        transform: `translate(-${this.x}px, -${this.y}px)`
      }
    },
  },
  watch: {
    imageUrl() {
      this.isImageLoaded = false
    },
  },
  methods: {
    hideSelf() {
      this.$emit('focusout')
    },
    fitDiv() {
      this.createImage(this.imageUrl)
          .then((image) => {
            this.areaWidth = this.$refs.cropper_area.clientWidth
            this.areaHeight = this.$refs.cropper_area.clientHeight

            this.originalWidth = image.width
            this.originalHeight = image.height

            const imageRatio = image.width / image.height
            const areaRatio = this.areaWidth / this.areaHeight

            if (imageRatio > areaRatio) {
              this.areaHeight = this.areaWidth / imageRatio
            } else {
              this.areaWidth = this.areaHeight * imageRatio
            }
            this.cropperAreaStyle.width = this.areaWidth + 'px'
            this.cropperAreaStyle.height = this.areaHeight + 'px'

            this.x = this.areaWidth / 2 - this.width / 2
            this.y = this.areaHeight / 2 - this.height / 2
          })
    },

    cropImage() {
      const multiplier = this.originalWidth / this.areaWidth
      const data = {
        sx: this.x * multiplier,
        sy: this.y * multiplier,
        sw: this.width * multiplier,
        sh: this.height * multiplier,
        dx: 0,
        dy: 0,
        dw: this.width,
        dh: this.height,
      }

      const context = this.$refs.canvas.getContext('2d')
      this.createImage(this.imageUrl).then((image) => {
        context.drawImage(image, data.sx, data.sy, data.sw, data.sh, data.dx, data.dy, data.dw, data.dh);

        let newImageUrl = this.$refs.canvas.toDataURL('image/png')
        this.$emit('new-image-url', newImageUrl)
        this.hideSelf()
      })
    },

    getPosition() {
      return {x: this.x, y: this.y}
    },
    getSize() {
      return {width: this.width, height: this.height}
    },

    isStillInside(newCoord, offset, limit) {
      return newCoord >= 0 && newCoord + offset <= limit
    },

    initializeMovable() {
      let el = this.$refs.cropper
      el.ondragstart = function () {
        return false;
      };

      el.onmousedown = (event) => {
        let initialX = event.clientX, initialY = event.clientY

        let posBeforeMove = this.getPosition()

        let onMouseMove = (event) => {
          const movedX = Math.round(event.clientX - initialX)
          const movedY = Math.round(event.clientY - initialY)

          const [newX, newY] = [posBeforeMove.x + movedX, posBeforeMove.y + movedY]
          if (this.isStillInside(newX, this.width, this.areaWidth)) {
            this.x = newX
          }
          if (this.isStillInside(newY, this.height, this.areaHeight)) {
            this.y = newY
          }
        }

        document.addEventListener('mousemove', onMouseMove);

        document.onmouseup = () => {
          document.removeEventListener('mousemove', onMouseMove);
          document.onmouseup = null;
        }
      };
    },

    initializeSizeChanger(el, isHorizontal, shouldChangePos) {
      el.ondragstart = function () {
        return false;
      };

      el.onmousedown = (event) => {
        event.stopPropagation()

        let initialX = event.clientX, initialY = event.clientY

        let sizeBeforeMove = this.getSize()
        let posBeforeMove = this.getPosition()

        let onMouseMove = (event) => {
          let movedX = Math.round(event.clientX - initialX)
          let movedY = Math.round(event.clientY - initialY)

          if (isHorizontal) {
            if (shouldChangePos) {
              if (this.isStillInside(posBeforeMove.x + movedX, sizeBeforeMove.width - movedX, this.areaWidth)) {
                this.x = posBeforeMove.x + movedX
                this.width = sizeBeforeMove.width - movedX
              }
            } else if (this.isStillInside(this.x, sizeBeforeMove.width + movedX, this.areaWidth)) {
              this.width = sizeBeforeMove.width + movedX
            }
          } else {
            if (shouldChangePos) {
              if (this.isStillInside(posBeforeMove.y + movedY, sizeBeforeMove.height - movedY, this.areaHeight)) {
                this.y = posBeforeMove.y + movedY
                this.height = sizeBeforeMove.height - movedY
              }
            } else if (this.isStillInside(this.y, sizeBeforeMove.height + movedY, this.areaHeight)) {
              this.height = sizeBeforeMove.height + movedY
            }
          }
        }

        document.addEventListener('mousemove', onMouseMove);

        document.onmouseup = () => {
          document.removeEventListener('mousemove', onMouseMove);
          document.onmouseup = null;
        }
      };
    },
  },
  created() {
  },
  mounted() {
    this.$emit('focusin')

    this.$nextTick().then(() => {
      this.fitDiv()
      this.initializeMovable()
      this.initializeSizeChanger(this.$refs.top, false, true)
      this.initializeSizeChanger(this.$refs.right, true, false)
      this.initializeSizeChanger(this.$refs.bottom, false, false)
      this.initializeSizeChanger(this.$refs.left, true, true)
    })
  },
}
</script>

<style scoped lang="scss">
@import "src/assets/styles/colors";

.overlay {
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1040;
  //backdrop-filter: blur(12px);

  cursor: pointer;

  align-items: center;
  justify-content: center;

  &.dark {
    background-color: rgba(0, 0, 0, 0.6);
  }

  ::v-deep .overlay_button {
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 6px;

    color: white;
    cursor: pointer;

    z-index: 1050;

    display: flex;
    justify-content: center;
    align-items: center;

    &.hide_button {
      position: fixed;
      top: 8px;
      right: 8px;

      height: 50px;
      width: 50px;
      font-size: 36px;
    }

    &.ok_button {
      position: fixed;
      left: 50%;
      bottom: 4%;
      transform: translateX(-50%);

      padding: 6px 12px;
      font-size: 2rem;
    }
  }

  .content {
    width: 100%;
    height: 100%;
    padding: 5% 5% 10%;

    display: flex;
    justify-content: center;
    align-items: center;

    .cropper_area {
      position: relative;

      img.cropping {
        height: 100%;
        width: 100%;
        filter: brightness(50%);
      }

      .cropper {
        position: absolute;
        left: 0;
        top: 0;

        outline: 2px $primary solid;

        cursor: move;
        user-select: none;

        .image_wrapper {
          width: 100%;
          height: 100%;
          overflow: hidden;
        }

        .size_change {
          $squareSize: 10px;
          width: $squareSize;
          height: $squareSize;
          background-color: $primary;
          position: absolute;
          z-index: 1;

          $moverOffset: 1px;

          &.top {
            top: 0;
            left: 50%;
            transform: translateY(calc(-50% - #{$moverOffset})) translateX(-50%);
            cursor: ns-resize;
          }

          &.right {
            right: 0;
            top: 50%;
            transform: translateX(calc(50% + #{$moverOffset})) translateY(-50%);
            cursor: ew-resize;
          }

          &.bottom {
            bottom: 0;
            left: 50%;
            transform: translateY(calc(50% + #{$moverOffset})) translateX(-50%);
            cursor: ns-resize;
          }

          &.left {
            left: 0;
            top: 50%;
            transform: translateX(calc(-50% - #{$moverOffset})) translateY(-50%);
            cursor: ew-resize;
          }
        }
      }
    }
  }
}
</style>
