<template>
  <transition name="fade">
    <div class="modal-mask" v-show="showModal">
      <div class="modal-wrapper">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">Вставить ссылку</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close" @click="showModal = false">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">

              <div class="form-group">
                <label for="urlInput">Ссылка:</label>
                <input type="url"
                       class="form-control"
                       id="urlInput"
                       v-model="url"
                >
              </div>

              <div class="form-group">
                <label for="textInput">Замещающий текст:</label>
                <input type="url"
                       class="form-control"
                       id="textInput"
                       v-model="text"
                >
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" @click="showModal = false">Отмена</button>
              <button type="button" class="btn btn-primary"
                      @click="addLink"
              >Добавить
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'linkModal',
  props: {},
  data: function () {
    return {
      showModal: false,
      url: '',
      text: '',
    }
  },
  computed: {},
  watch: {},
  methods: {
    toggle() {
      this.url = ''
      this.text = ''
      this.showModal = true
    },

    addLink() {
      this.showModal = false
      this.$emit('insert-link', this.url, this.text)
    },
  },
}
</script>

