<script>
import {mapActions, mapGetters, mapMutations} from "vuex";
import {ADD_ANSWER_TO_ANSWERS_ELEMENT} from "@/store/mutations/mutation-types";
import {ADD_ANSWER_ACTION} from "@/store/actions/action-types";

export default {
  name: 'AddAnswer',
  computed: {
    ...mapGetters(['activeSlide'])
  },
  methods: {
    ...mapMutations({
      addAnswerToAnswersElement: ADD_ANSWER_TO_ANSWERS_ELEMENT,
    }),
    ...mapActions({
      addValuableAnswerAction: ADD_ANSWER_ACTION,
    }),
    addAnswer() {
      this.addValuableAnswerAction()
          .then(answer => {
            const answersElement = this.activeSlide.slideElements.find(el => el.content.component === 'AnswersObject')
            this.addAnswerToAnswersElement({id: answersElement.id, answer})
          })
    },
  },
}
</script>
