<template>
  <div class="image_object">
    <input type="file"
           accept="image/x-png,image/gif,image/jpeg"
           @change="onUpload"
           ref="fileInput"
    >

    <div
        class="help_text"
        v-if="url === null"
    >
      Загрузите изображение
    </div>

    <img
        v-else
        :src="url"
        :class="{fill: isFilling}"
    >

    <image-resize-modal
        v-if="showResizeModal"
        :tabindex="0"
        :image-url="url"
        @focusin="$emit('focusin')"
        @focusout="showResizeModal = false; $emit('focusout')"
        @new-image-url="onImageCrop"
    />
  </div>
</template>

<script>
import ImageResizeModal from "@/components/slide-elements/image-object/ImageResizeModal";

export default {
  name: 'ImageObject',
  components: {ImageResizeModal},
  props: ['value'],
  data() {
    return {
      isFilling: false,

      file: null,
      image: null,
      url: null,

      showResizeModal: false,
    }
  },
  computed: {},
  watch: {
    isFilling() {
      this.updateActions()
      this.updateValue()
    },
    url() {
      this.updateActions()
      this.updateValue()
    },
  },
  methods: {
    createImage(url) {
      return new Promise(function (resolve, reject) {
        let img = new Image()
        img.onload = function () {
          resolve(this)
        }
        img.onerror = reject
        img.src = url;
      })
    },

    onUpload(event) {
      let hasOversize = false
      this.file = event.target.files[0]

      if (this.file.size > 300 * 1024)
        hasOversize = true
      if (hasOversize) {
        alert('Максимальный размер одного файла - 300 Кбайт')
        this.$refs.fileInput.value = null
        this.file = null
        return
      }

      this.url = URL.createObjectURL(this.file)
      this.createImage(this.url).then((image) => this.image = image)
    },
    onImageCrop(newImageUrl) {
      this.url = newImageUrl
    },

    updateActions() {
      this.$emit('initialized', {
        actions: [
          {html: '<i class="fas fa-upload"></i>', func: this.uploadImage},
          {html: '<i class="fas fa-crop-alt"></i>', func: this.cropImage, disabled: !this.url},
          {html: '<i class="fas fa-arrows-alt"></i>', func: this.toggleFilling, isPressed: this.isFilling},
        ],
        onDblClick: this.toggleFilling,
      })
    },
    updateValue() {
      this.$emit('input', {url: this.url, fill: this.isFilling})
    },

    uploadImage() {
      this.$refs.fileInput.click()
    },
    cropImage() {
      this.showResizeModal = true
    },
    toggleFilling() {
      this.isFilling = !this.isFilling
    },
  },
  provide: function () {
    return {
      createImage: this.createImage
    }
  },
  created() {
    this.url = this.value.url
    this.isFilling = this.value.fill
  },
  mounted() {
    this.updateActions()
  },
}
</script>

<style scoped lang="scss">
.image_object {
  input[type=file] {
    display: none;
  }

  .help_text {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    padding: 0;

    font-size: 1.1em;
    color: #8c8c8c;
  }

  img {
    height: 100%;
    width: 100%;
    object-fit: contain;

    &.fill {
      object-fit: fill;
    }
  }
}
</style>
