<template>
  <div
      class="content_wrapper"
      :class="{entirely_visible: fitContent}"
      ref="content_wrapper"
  >
    <div
        class="content"
        ref="content"
        :style="contentStyles"
    >
      <slot v-if="ready"></slot>
    </div>
  </div>
</template>

<script>
import {mapMutations} from "vuex";
import {TOGGLE_SCROLLBARS} from "@/store/mutations/mutation-types";

export default {
  name: 'RatioKeeper',
  props: {
    ratio: Number,
    minHeight: {
      type: Number,
      default: 0,
    },
    fitContent: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      ready: false,
      height: 0,
      scale: 1,

      offsetLeft: 0,
      offsetTop: 0,

      paddingX: 20,
      paddingY: 16,
      // horizontalScrollPadding: 0,
      horizontalScrollPadding: 20,
    }
  },
  computed: {
    contentWidth() {
      return this.contentHeight * this.ratio + this.paddingX * 2
    },
    contentHeight() {
      return this.height - this.paddingY * 2
    },
    contentStyles() {
      if (this.fitContent) {
        return {
          'min-width': this.contentWidth + 'px',
          height: this.height + 'px',
          padding: `${this.paddingY}px ${this.paddingX}px`,
          transform: `scale(${this.scale})`,
          overflow: 'hidden',
        }
      } else {
        return {
          width: this.contentWidth + 'px',
          height: this.height + 'px',
          padding: `${this.paddingY}px ${this.paddingX}px`,
        }
      }
    },
  },
  watch: {
    minHeight() {
      this.keepAspectRatio()
    },
    ratio() {
      this.keepAspectRatio()
    },
    fitContent() {
      this.keepAspectRatio()
    },
  },
  methods: {
    ...mapMutations({
      toggleScrollbars: TOGGLE_SCROLLBARS,
    }),
    getBaseHeight() {
      const newHeight = this.$refs.content_wrapper.clientHeight - this.horizontalScrollPadding
      if (newHeight > this.minHeight) {
        return newHeight
      } else {
        return this.minHeight
      }
    },
    getScale() {
      const widthScale = this.$refs.content_wrapper.clientWidth / this.$refs.content.clientWidth
      const heightScale = this.$refs.content_wrapper.clientHeight / this.$refs.content.clientHeight
      return Math.min(widthScale, heightScale)
    },

    async keepAspectRatio() {
      this.ready = false

      this.height = this.getBaseHeight()

      this.$nextTick(() => {
        if (this.fitContent) {
          this.scale = this.getScale()
        } else {
          this.scale = 1
        }

        this.ready = true
        this.$emit('ready')
      })
    },
  },
  created() {
    const keepAspectRatioCallBack = () => this.keepAspectRatio()

    window.addEventListener('resize', keepAspectRatioCallBack)
    this.$once('hook:beforeDestroy', function () {
      window.removeEventListener('resize', keepAspectRatioCallBack)
    })
  },
  mounted() {
    this.keepAspectRatio()
  },
}
</script>

<style scoped lang="scss">
.content_wrapper {
  width: 100%;
  height: 100%;

  &.entirely_visible {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .content {
    margin: 0 auto;
  }
}
</style>
