<template>
  <div class="sidebar border-end">
    <div
        class="slides"
        ref="slides"
    >
      <div
          v-for="(slideId, index) in slideIds"
          :key="slideId"
          class="sidebar_item"
          :class="{selected: isSlideSelected(slideId)}"
          @click="setActiveSlide(slideId)"
      >
        <div class="actions_panel">
          <div>
            <b>{{ slideId }}</b>
          </div>
          <div style="flex: 1 1 auto"></div>

          <div class="actions">
            <div
                v-if="index !== 0"
                class="action"
                @click.stop="upperSlide(slideId)"
            >
              <i class="fa fa-fw fa-sm fa-arrow-up"></i>
            </div>

            <div
                v-if="index !== slideIds.length - 1"
                class="action"
                @click.stop="lowerSlide(slideId)"
            >
              <i class="fa fa-fw fa-sm fa-arrow-down"></i>
            </div>

            <div
                class="action"
                @click.stop="duplicateSlide(slideId)"
            >
              <i class="fa fa-fw fa-sm fa-clone"></i>
            </div>

            <div
                class="action"
                @click.stop="deleteSlide(slideId)"
            >
              <i class="fa fa-fw fa-trash-o"></i>
            </div>
          </div>
        </div>

        <SlidePreview :slide-id="slideId"/>
      </div>
    </div>

    <div class="controls" :class="{ 'border-top': hasScroll }">
      <button
          type="button"
          class="btn btn-primary btn-lg"
          @click="addSlide"
      >Добавить слайд
      </button>
    </div>
  </div>
</template>

<script>
import SlidePreview from "@/components/SlidePreview";
import {mapActions, mapGetters, mapMutations} from "vuex";
import {SET_ACTIVE_SLIDE} from "@/store/mutations/mutation-types";
import {
  ADD_SLIDE_ACTION,
  DELETE_SLIDE_ACTION,
  DUPLICATE_SLIDE_ACTION, LOWER_SLIDE_ACTION,
  UPPER_SLIDE_ACTION
} from "@/store/actions/action-types";

export default {
  name: 'Sidebar',
  components: {
    SlidePreview,
  },
  props: {},
  data() {
    return {
      mounted: false,
    }
  },
  computed: {
    ...mapGetters([
      'slideIds',
    ]),
    hasScroll() {
      if (!this.mounted) return false
      else return this.$refs.slides.scrollHeight > this.$refs.slides.clientHeight
    },
  },
  methods: {
    ...mapMutations({
      setActiveSlide: SET_ACTIVE_SLIDE,
    }),
    ...mapActions({
      addSlide: ADD_SLIDE_ACTION,
      upperSlide: UPPER_SLIDE_ACTION,
      lowerSlide: LOWER_SLIDE_ACTION,
      duplicateSlide: DUPLICATE_SLIDE_ACTION,
      deleteSlide: DELETE_SLIDE_ACTION,
    }),
    isSlideSelected(id) {
      if (this.$store.getters.activeSlide === undefined) return false
      return id === this.$store.getters.activeSlide.id
    },
  },
  mounted() {
    this.mounted = true
  }
}
</script>

