    .sidebar_item {
      padding: .5rem .7rem .5rem 0;
      display: flex;
      cursor: pointer;

      &:hover {
        .actions_panel .actions {
          opacity: 1;
        }
      }

      .actions_panel {
        min-width: 2.5rem;
        padding: 0 .4rem;
        display: flex;
        flex-direction: column;
        align-items: center;

        .actions {
          opacity: 0;
          width: 100%;
          flex-direction: column;
          align-items: center;
          text-align: center;

          font-size: .9rem;

          .action {
            width: 100%;
            display: flex;
            justify-content: center;

            margin-bottom: 2px;
            padding: 4px 2px;

            &:last-child {
              margin: 0;
            }

            &:hover {
              border-radius: 4px;
              background: $buttonHover;
            }
          }
        }
      }
    }

    .selected {
      background-color: #daf0fc;
    }
  }

