<template>
  <div class="youtube_object">
    <div
        v-if="videoId === null"
        class="help_text"
    >
      Укажите ссылку на видео YouTube
    </div>

    <iframe
        v-else
        :src="iframeSrc"
        title="YouTube video player"
        frameborder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowfullscreen
    ></iframe>

    <you-tube-link-modal ref="linkAddModal"
                         @set-video-id="onSetVideoId"
    />
  </div>
</template>

<script>
import YouTubeLinkModal from "@/components/slide-elements/youtube-object/YouTubeLinkModal";

export default {
  name: 'YouTubeObject',
  components: {YouTubeLinkModal},
  props: ['value'],
  data() {
    return {
      videoId: null,
      // videoId: '6FRonW4oSao',
      autoplay: false,
    }
  },
  computed: {
    iframeSrc() {
      return `https://www.youtube.com/embed/${this.videoId}?autoplay=${this.autoplay ? 1 : 0}`
    },
  },
  watch: {
    videoId() {
      this.updateActions()
      this.updateValue()
    },
    autoplay() {
      this.updateActions()
      this.updateValue()
    },
  },
  methods: {
    updateActions() {
      this.$emit('initialized', {
        actions: [
          {html: '<i class="fas fa-upload"></i>', func: this.$refs.linkAddModal.toggle},
          {html: '<i class="far fa-play-circle"></i>', func: this.toggleAutoplay, isPressed: this.autoplay},
        ],
        onDblClick: this.toggleAutoplay,
      })
    },
    updateValue() {
      this.$emit('input', {videoId: this.videoId, autoplay: this.autoplay})
    },

    onSetVideoId(videoId) {
      console.log(videoId)
      this.videoId = videoId
    },
    toggleAutoplay() {
      this.autoplay = !this.autoplay
    },
  },
  created() {
    this.videoId = this.value.videoId
    this.autoplay = this.value.autoplay
  },
  mounted() {
    this.updateActions()
  },
}
</script>

<style scoped lang="scss">
.youtube_object {
  iframe {
    height: 100%;
    width: 100%;
  }

  .help_text {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    padding: 0;

    font-size: 1.1em;
    color: #8c8c8c;
  }
}
</style>
