import {SET_SLIDE_HEIGHT, TOGGLE_GRID, TOGGLE_SCROLLBARS} from "@/store/mutations/mutation-types";

export default {
    [SET_SLIDE_HEIGHT](state, height) {
        state.slideParams.height = height
        state.slideParams.cellSize = height / state.slideParams.heightCells
    },
    [TOGGLE_GRID](state) {
        state.slideParams.showGrid = !state.slideParams.showGrid
    },
    [TOGGLE_SCROLLBARS](state) {
        state.slideParams.isEntirelyVisible = !state.slideParams.isEntirelyVisible
    },
}