<script>
export default {
  name: 'Restricted',
  data() {
    return {
      restrictions: {},
    }
  },
  methods: {
    countElementsOnSlide(elName) {
      return this.$store.getters.activeSlideElements.filter(el => el.content.component === elName).length
    },
    tryAction(elName, action, payload) {
      if (!(elName in this.restrictions))
        alert('elName not in this.restrictions')
      else if (this.countElementsOnSlide(elName) < this.restrictions[elName].max)
        this.$store.dispatch(action, payload)
      else alert(`На слайде уже есть ${this.restrictions.TextObject.max} ${elName}`)
    },
  },
  provide: function () {
    return {
      canDeleteElement: (elName) => {
        return !(elName in this.restrictions) || this.countElementsOnSlide(elName) > this.restrictions[elName].min
            || `На слайде должно быть минимум ${this.restrictions[elName].min} ${elName}`
      },
    }
  },
  created() {
    if (Object.keys(this.restrictions).length === 0)
      console.warn('Похоже, что для типа слайда не заданы restrictions, но примесь используется')
  },
}
</script>
