import {
    ADD_IMAGE_ELEMENT, ADD_MULTIPLE_CHOICE_ANSWERS_ELEMENT, ADD_SINGLE_CHOICE_ANSWERS_ELEMENT,
    ADD_TEXT_ELEMENT,
    ADD_TIMER_ELEMENT,
    ADD_ANSWER_ACTION, ADD_VOTING_ANSWERS_ELEMENT,
    ADD_YES_NO_ANSWERS_ELEMENT,
    ADD_YOUTUBE_ELEMENT,
    REMOVE_ANSWER_ACTION,
    SET_SLIDE_TIME_ACTION,
    SET_SLIDE_TYPE_ACTION
} from "@/store/actions/action-types";
import {
    ADD_SLIDE_ELEMENT,
    DELETE_SLIDE_ELEMENT, SET_SLIDE_PRICE,
    SET_SLIDE_TIME,
    SET_SLIDE_TYPE
} from "@/store/mutations/mutation-types";
import initialStates from "@/components/slide-types/initial-state";

let answerIdCounter = 1

export default {
    [SET_SLIDE_TYPE_ACTION]({getters, commit, dispatch}, type) {
        getters.activeSlide.slideElements.forEach(el => {
            if (el.isSpecific) {
                commit(DELETE_SLIDE_ELEMENT, el.id)
            }
        })
        commit(SET_SLIDE_TYPE, type)
        commit(SET_SLIDE_PRICE, 0)
        if (initialStates[getters.activeSlideComponent]) {
            initialStates[getters.activeSlideComponent].forEach(operation => dispatch(operation.action))
        }
    },
    // eslint-disable-next-line no-unused-vars
    [SET_SLIDE_TIME_ACTION]({getters, commit, dispatch}, time) {
        const hasTimerObject = getters.activeSlide.time > 0

        time = parseInt(time)
        if (isNaN(time) || time < 0)
            time = 0

        commit(SET_SLIDE_TIME, time)

        if (time === 0 && hasTimerObject) {
            const timerElement = getters.activeSlide.slideElements.find(el => el.content.component === 'TimerObject')
            commit(DELETE_SLIDE_ELEMENT, timerElement.id)
        } else if (time > 0 && !hasTimerObject) {
            dispatch(ADD_TIMER_ELEMENT, {x: 1, y: 30, width: 5, height: 5, noResize: true, noActions: true})
        }
    },
    [ADD_TIMER_ELEMENT]({commit}, params) {
        commit(ADD_SLIDE_ELEMENT, {
            ...params,
            content: {component: 'TimerObject', value: null},
        })
    },
    [ADD_TEXT_ELEMENT]({commit}, params) {
        commit(ADD_SLIDE_ELEMENT, {
            ...params,
            content: {
                component: 'TextObject', value: {
                    html: '',
                }
            },
        })
    },
    [ADD_IMAGE_ELEMENT]({commit}, params) {
        commit(ADD_SLIDE_ELEMENT, {
            ...params,
            content: {
                component: 'ImageObject', value: {
                    url: null,
                    fill: false,
                }
            },
        })
    },
    [ADD_YOUTUBE_ELEMENT]({commit}, params) {
        commit(ADD_SLIDE_ELEMENT, {
            ...params,
            content: {
                component: 'YouTubeObject', value: {
                    videoId: null,
                    autoplay: false,
                }
            },
        })
    },
    // [ADD_ANSWER_ACTION]() {
    //     return new Promise((resolve) => {
    //         resolve({id: answerIdCounter++, text: ''})
    //     })
    // },
    [ADD_ANSWER_ACTION]() {
        return new Promise((resolve) => {
            resolve({id: answerIdCounter++, text: '', is_correct: false})
        })
    },
    // eslint-disable-next-line no-unused-vars
    [REMOVE_ANSWER_ACTION](answerId) {
        return new Promise((resolve) => {
            resolve()
        })
    },

    [ADD_YES_NO_ANSWERS_ELEMENT]({commit, dispatch}) {
        let answers = []
        dispatch(ADD_ANSWER_ACTION)
            .then(answer1 => {
                answer1.text = '<p style="text-align: center;"><span class="wrapper" style="font-size: 24pt;">Да</span></p>'
                answers.push(answer1);
                return dispatch(ADD_ANSWER_ACTION)
            })
            .then(answer2 => {
                answer2.text = '<p style="text-align: center;"><span class="wrapper" style="font-size: 24pt;">Нет</span></p>'
                answers.push(answer2)
            }).then(() => {
            if (!answers.map(answer => answer.is_correct).includes(true))
                answers[0].is_correct = true

            commit(ADD_SLIDE_ELEMENT, {
                x: 12,
                y: 27,
                width: 49,
                height: 5,
                content: {
                    component: 'AnswersObject', value: {
                        answers,
                        columnsCount: 2,
                        fixedAnswersCount: true,
                        isValuable: true,
                        isSingleChoice: true,
                    }
                },
                noDelete: true,
                isSpecific: true,
            })
        })
    },
    [ADD_SINGLE_CHOICE_ANSWERS_ELEMENT]({commit, dispatch}) {
        let answers = []
        dispatch(ADD_ANSWER_ACTION)
            .then(answer1 => {
                answers.push(answer1);
                return dispatch(ADD_ANSWER_ACTION)
            })
            .then(answer2 => {
                answers.push(answer2);
                return dispatch(ADD_ANSWER_ACTION)
            })
            .then(answer3 => {
                answers.push(answer3);
                return dispatch(ADD_ANSWER_ACTION)
            })
            .then(answer4 => answers.push(answer4)).then(() => {
            if (!answers.map(answer => answer.is_correct).includes(true))
                answers[0].is_correct = true

            commit(ADD_SLIDE_ELEMENT, {
                x: 9,
                y: 26,
                width: 52,
                height: 8,
                content: {
                    component: 'AnswersObject', value: {
                        answers,
                        columnsCount: 2,
                        isValuable: true,
                        isSingleChoice: true,
                    }
                },
                noDelete: true,
                isSpecific: true,
            })
        })
    },
    [ADD_MULTIPLE_CHOICE_ANSWERS_ELEMENT]({commit, dispatch}) {
        let answers = []
        dispatch(ADD_ANSWER_ACTION)
            .then(answer1 => {
                answers.push(answer1);
                return dispatch(ADD_ANSWER_ACTION)
            })
            .then(answer2 => {
                answers.push(answer2);
                return dispatch(ADD_ANSWER_ACTION)
            })
            .then(answer3 => {
                answers.push(answer3);
                return dispatch(ADD_ANSWER_ACTION)
            })
            .then(answer4 => answers.push(answer4)).then(() => {
            if (!answers.map(answer => answer.is_correct).includes(true))
                answers[0].is_correct = true

            commit(ADD_SLIDE_ELEMENT, {
                x: 9,
                y: 26,
                width: 52,
                height: 8,
                content: {
                    component: 'AnswersObject', value: {
                        answers,
                        columnsCount: 2,
                        isValuable: true,
                    }
                },
                noDelete: true,
                isSpecific: true,
            })
        })
    },
    [ADD_VOTING_ANSWERS_ELEMENT]({commit, dispatch}) {
        let answers = []
        dispatch(ADD_ANSWER_ACTION)
            .then(answer1 => {
                answers.push(answer1);
                return dispatch(ADD_ANSWER_ACTION)
            })
            .then(answer2 => {
                answers.push(answer2);
                return dispatch(ADD_ANSWER_ACTION)
            })
            .then(answer3 => {
                answers.push(answer3);
                return dispatch(ADD_ANSWER_ACTION)
            })
            .then(answer4 => answers.push(answer4)).then(() => {

            commit(ADD_SLIDE_ELEMENT, {
                x: 9,
                y: 26,
                width: 52,
                height: 8,
                content: {
                    component: 'AnswersObject', value: {
                        answers,
                        columnsCount: 2,
                    }
                },
                noDelete: true,
                isSpecific: true,
            })
        })
    },
}